<template>
  <div class="ic-hamburger" :class="{active: isActive}" @click="toggleMenu">
    <span class="line"></span>
    <span class="line"></span>
    <span class="line"></span>
  </div>
</template>

<script>
export default {
  props: {
    isActive: Boolean
  },
  methods: {
    toggleMenu () {
      this.$emit('update:isActive', !this.isActive)
    }
  }
}
</script>

<style lang="less" scoped>
.ic-hamburger {
  margin-right: 15px;
  display: none;
  .line {
    display: block;
    width: 20px;
    height: 2px;
    margin: 4px auto;
    border-radius: 1px;
    background: #fff;
    transition: all .3s ease-in-out;
  }
  &.active {
    .line:first-child {
      transform: translateY(6px) rotate(45deg);
    }
    .line:nth-child(2) {
      opacity: 0;
    }
    .line:nth-child(3) {
      transform: translateY(-6px) rotate(-45deg);
    }
  }
  @media @max768 {
    display: block;
  }
}
</style>
