<template>
  <div class="page-header">
    <div class="page-header_inner">
      <div class="page-header_lt"></div>
      <nav class="page-header_center">
        <router-link
          class="nav"
          :class="{'nav-active': activeNav === item.key}"
          v-for="item in NAV_MENU"
          :key="item.name"
          :to="item.href">{{ item.name }}
        </router-link>
      </nav>
      <div class="page-header_rt">
        <!-- <span class="btn-login" @click="linkToPath('https://dapp.ctsign.cn/')">登录</span> -->
        <button class="btn-register" @click="connectWallet()">{{ stateText }}</button>
        <span class="btn-lang"></span>
      </div>
      <Hamburger :isActive.sync="isShowHamburger"/>
    </div>
    <div class="m-menu-wrap" :class="{active: isShowHamburger}">
      <ul class="m-menu">
        <li
          v-for="item in NAV_MENU"
          :key="item.name"
          :class="{active: activeNav === item.key}"
          @click="linkNav(item.href)"
        >{{ item.name }}
        </li>
      </ul>
      <div class="login-register">
        <!--<button class="btn-register" @click="linkToPath('https://h5.ctsign.cn/')">登录</button>-->
        <button class="btn-login" @click="connectWallet()">{{ stateText }}</button>
      </div>
    </div>
  </div>
</template>

<script>
import Hamburger from '@/components/Hamburgr'
import { NAV_MENU } from '@/constant'

export default {
  components: {
    Hamburger
  },
  data () {
    return {
      NAV_MENU,
      defaultNav: 'home',
      isShowHamburger: false,
      stateText: '链接钱包'
    }
  },
  computed: {
    activeNav () {
      const path = this.$route.path
      if (path === '/nftMultiple') {
        return 'nftMultiple'
      }
      return 'home'
    }
  },
  async mounted () {
    const _this = this
    this.$API.onboard.walletSelect().then(ret => {
      this.$API.onboard.walletCheck()
    })
    await setInterval(async () => {
      // if (await this.$API.onboard.walletCheck()) {
      if (await this.$API.onboard.getState().address) {
        _this.stateText = '切换钱包'
      } else {
        _this.stateText = '链接钱包'
      }
    }, 500)
  },
  methods: {
    linkNav (href) {
      this.isShowHamburger = false
      this.$router.push({
        path: href
      })
    },
    linkToPath (path) {
      window.location.href = path
    },
    connectWallet () {
      this.isShowHamburger = false
      this.$API.onboard.walletSelect().then(ret => {
        this.$API.onboard.walletCheck()
      })
    }
  }
}
</script>

